import React, { Component } from 'react';

class Layout extends Component {
    constructor( props ) {
        super( props )
    }

    render() {
        return (
            <React.Fragment>
                <p>This is My Layout</p>
                { this.props.children }
            </React.Fragment>
        )    
    }
}

export default Layout


